.all {
  margin: 0;
  padding: 0;
  background-color: #f8f9fa;
  overflow-x: hidden;
}

.header2 {
  height: 400px;
  width: 100%;
  background-color: #4e00ff;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px;
}

.Parts {
  padding: 40px 20px;
  max-width: 1000px;
  margin: auto;
}

h2 {
  color: #4e00ff;
  margin-bottom: 10px;
}

.features {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.feature-box {
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.screenshots {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.screenshots img {
  width: 250px;
  border-radius: 10px;
  border: 1px solid #ddd;
}

.testimonials {
  background: #eee;
  padding: 20px;
  border-radius: 8px;
}

.team {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.team-member {
  background: white;
  padding: 15px;
  border-radius: 8px;
  text-align: center;
  flex: 1;
  min-width: 200px;
  max-width: 300px;
}

.download {
      background-image: url(../../public/img/photo-1620207418302-439b387441b0.jpg);
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  filter: brightness(0.999);
  padding: 60px 20px;
  text-align: center;
}

.download h2 {
  color: white;
}

.download button {
  display: inline-block;
  margin: 10px;
  padding: 15px 25px;
  color: white;
  border-radius: 5px;
}

.download:hover {
  cursor: pointer;
  filter: brightness(.8);
}

/* -------------------- Responsive Styles -------------------- */

@media only screen and (max-width: 600px) {
  .features {
    grid-template-columns: 1fr;
  }

  .screenshots img {
    width: 90%;
  }

  .team {
    flex-direction: column;
    align-items: center;
  }

  .team-member {
    width: 90%;
  }

  .header2 {
    height: auto;
    padding: 60px 20px;
  }
}

@media only screen and (min-width: 600px) and (max-width: 900px) {
  .features {
    grid-template-columns: repeat(2, 1fr);
  }

  .screenshots img {
    width: 45%;
  }

  .team-member {
    min-width: 45%;
  }
}

@media only screen and (min-width: 900px) and (max-width: 1200px) {
  .features {
    grid-template-columns: repeat(3, 1fr);
  }
}